import React, { useState, useEffect } from "react";
import { io } from "socket.io-client";
import logo from "./logo.png";
import "./Etudiant.css";

// Initialisation unique du socket
const socket = io("http://localhost:5000");

function Etudiant() {
  const [message, setMessage] = useState("Attendez"); // Message par dfaut
  const [heure, setHeure] = useState(new Date());
  const [isDemandeEnvoyee, setIsDemandeEnvoyee] = useState(false); // Etat pour savoir si la demande d'entre a t envoye

  useEffect(() => {
    // Met  jour l'heure chaque seconde
    const timer = setInterval(() => {
      setHeure(new Date());
    }, 1000);

    // coute les messages venant de la secrtaire
    socket.on("update-etudiant", (msg) => {
      console.log("Message reu :", msg);
      setMessage(msg);
    });

    return () => {
      clearInterval(timer);
      socket.off("update-etudiant");
    };
  }, []);

  // Fonction pour grer l'envoi de la demande d'entre
  const envoyerDemandeEntree = () => {
    // Envoi de la demande au serveur via socket
    socket.emit("demande-entre", "Un tudiant demande  entrer.");
    setIsDemandeEnvoyee(true); // Marque la demande comme envoye
    console.log("Demande d'entre envoye !");
  };

  const getButtonColor = () => {
    switch (message) {
      case "Entrez": return "green";
      case "Attendez": return "yellow";
      case "Ferm": return "red";
      default: return "gray";
    }
  };

  return (
    <div className="etudiant-container">
      <img src={logo} alt="Universit de Lorraine" className="logo" />
      <h1>Bienvenue</h1>
      <p className="date">{heure.toLocaleDateString()} - {heure.toLocaleTimeString()}</p>
      
      {/* Affichage du bouton de rponse */}
      <button className="response-button" style={{ backgroundColor: getButtonColor() }}>
        {message}
      </button>

      {/* Bouton pour envoyer la demande d'entre */}
      {!isDemandeEnvoyee ? (
        <button 
          className="demande-button" 
          onClick={envoyerDemandeEntree} 
          style={{ backgroundColor: "blue", color: "white" }}
        >
          Demande d'entre
        </button>
      ) : (
        <p>Votre demande a t envoye. Veuillez attendre la rponse.</p>
      )}
    </div>
  );
}

export default Etudiant;
