import React, { useState, useEffect } from "react";
import { io } from "socket.io-client";
import { Link, useNavigate } from "react-router-dom";
import "./Secretaire.css";


// Initialisation du socket
const socket = io("http://localhost:5000", { autoConnect: false });

function Secretaire({ onLogout }) {
  const [inputMessage, setInputMessage] = useState("");
  const [notifications, setNotifications] = useState(0); // Compteur de notifications
  const [isRequestOpen, setIsRequestOpen] = useState(false); // tat pour afficher les boutons "Entrez" et "Attendez"
  const [flash, setFlash] = useState(false); // tat pour afficher un flash visuel
  const navigate = useNavigate();
  const [isHorairesOpen, setIsHorairesOpen] = useState(false); // Nouvel tat pour grer les horaires
  
  // Vrifier si l'utilisateur est connect
  useEffect(() => {
    const token = localStorage.getItem("token");
    if (!token) {
      navigate("/login"); // Rediriger vers login si pas de token
    }
  }, [navigate]);

  // Gestion de la connexion WebSocket
  useEffect(() => {
    if (!socket.connected) socket.connect();

    // coute les demandes d'entre des tudiants
    socket.on("demande-entre", (data) => {
      console.log("Nouvelle demande d'entre reue:", data);
      setNotifications(prev => prev + 1); // Incrmente le compteur de notifications

      // Flash de notification
      setFlash(true);
      setTimeout(() => setFlash(false), 1000); // Flash dure 1 seconde
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  const envoyerMessage = (msg) => {
    if (!msg.trim()) return; // Empche l'envoi de messages vides
    socket.emit("secretaire-action", msg, (ack) => {
      if (ack?.error) console.error("Erreur d'envoi :", ack.error);
      else console.log("Message envoy avec succs !");
    });
  };

  const handleLogout = () => {
    localStorage.removeItem("token"); // Supprime le token JWT lors de la dconnexion
    onLogout(); // Dconnecte la secrtaire
    navigate("/login"); // Redirige vers la page de connexion
  };

  // Afficher ou masquer les boutons de rponse
  const openRequestResponse = () => {
    setIsRequestOpen(true);
    setNotifications(0); // Rinitialiser les notifications aprs avoir rpondu
  };

   // Afficher ou masquer la section des horaires
  const openHorairesResponse = () => {
    setIsHorairesOpen(true);
    setNotifications(0); // Rinitialiser les notifications aprs avoir rpondu
  };

  // Rpondre  la demande avec un message spcifique
  const handleResponse = (responseMessage) => {
    envoyerMessage(responseMessage); // Envoyer le message  l'tudiant
    setIsRequestOpen(false); // Fermer la fentre de rponse aprs envoi
  };

  return (
    <div className="secretaire-container">
      <h1>Interface Secrtaire</h1>

      {/* Affichage des notifications avec flash */}
      <div className="notifications">
        <button className={`notification-button ${flash ? "flash" : ""}`} onClick={openRequestResponse}>
          {notifications > 0 ? `Nouvelle demande (${notifications})` : "Pas de nouvelles demandes"}
        </button>
      </div>

      {isRequestOpen && (
        <div className="request-response">
          <h3>Rpondre  la demande d'entre :</h3>
          <button onClick={() => handleResponse("Entrez")} style={{ backgroundColor: "green" }}>
            Entrez
          </button>
          <button onClick={() => handleResponse("Attendez")} style={{ backgroundColor: "yellow" }}>
            Attendez
          </button>
        </div>
      )}


       {/* Affichage des horaires avec flash */}
       <div className="Horaires">
        <button className={`horaires-button ${flash ? "flash" : ""}`} onClick={openHorairesResponse}>
        {notifications === 0 ? "Etat actuel" : `Horaires (${notifications})`}

        </button>
      </div>

      {isHorairesOpen && (
        <div className="request-response">
          <h3>Rpondre  l'tat des horaires :</h3>
          <button onClick={() => handleResponse("OUVERT")} style={{ backgroundColor: "green" }}>
            OUVERT
          </button>
          <button onClick={() => handleResponse("FERMER")} style={{ backgroundColor: "yellow" }}>
            FERMER
          </button>
        </div>
      )}

      <div>
        <input
          type="text"
          placeholder="Message personnalis"
          value={inputMessage}
          onChange={(e) => setInputMessage(e.target.value)}
        />
        <button onClick={() => envoyerMessage(inputMessage)}>Envoyer</button>
      </div>

      <Link to="/parametres">
        <button>Accder aux Paramtres</button>
      </Link>
      {/* Ajout d'un lien vers la page Admin */}
      <Link to="/admin">
        <button>Accder  l'Administration</button>
      </Link>
      <button onClick={handleLogout}>Dconnexion</button>
    </div>
  );
}

export default Secretaire;
