import React, { useState, useEffect } from "react";
import axios from "axios";

function Admin() {
  const [secretaires, setSecretaires] = useState([]);
  const [formData, setFormData] = useState({
    nom: "",
    prenom: "",
    email: "",
    motDePasse: "",
    id: "", // Utilis pour la modification
  });
  const [action, setAction] = useState(""); // Action courante : "add", "update", ou "delete"

  // Rcuprer la liste des secrtaires depuis l'API
  useEffect(() => {
    const token = localStorage.getItem("token"); // Rcupre le token stock
    console.log("Token rcupr:", token);  // Ajoute ceci pour dboguer
    axios.get("http://localhost:5000/api/secretaires", {
      headers: {
        Authorization: `Bearer ${token}` // Ajoute le token JWT dans l'en-tte
      }
    })
    .then(response => {
      setSecretaires(response.data);
    })
    .catch(error => console.error("Erreur lors de la rcupration des secrtaires:", error));
  }, []);
  
  
  

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };
  const handleAddSecretaire = () => {
    const token = localStorage.getItem("token");
  
    axios.post("http://localhost:5000/api/secretaires", formData, {
      headers: {
        Authorization: `Bearer ${token}`
      }
    })
    .then(response => {
      setFormData({ nom: "", prenom: "", email: "", motDePasse: "", id: "" }); // Rinitialiser les champs
      alert("Secrtaire ajout avec succs !");
      
      // Recharger la liste des secrtaires aprs ajout
      axios.get("http://localhost:5000/api/secretaires", {
        headers: {
          Authorization: `Bearer ${token}`
        }
      })
      .then(response => setSecretaires(response.data))
      .catch(error => console.error("Erreur lors de la mise  jour de la liste:", error));
    })
    .catch(error => console.error("Erreur lors de l'ajout :", error));
  };
  

  const handleDeleteSecretaire = (id) => {
    const token = localStorage.getItem("token"); // Rcupre le token
  
    if (window.confirm("tes-vous sr de vouloir supprimer ce secrtaire ?")) {
      axios.delete(`http://localhost:5000/api/secretaires/${id}`, {
        headers: {
          Authorization: `Bearer ${token}` // Envoie le token JWT
        }
      })
      .then(() => {
        setSecretaires(secretaires.filter(sec => sec._id !== id));
        alert("Secrtaire supprim avec succs !");
      })
      .catch(error => console.error("Erreur lors de la suppression :", error));
    }
  };
  
  

  return (
    <div>
      <h1>Gestion des secrtaires</h1>

      {/* Formulaire de gestion */}
      {action === "add" && (
        <div>
          <h2>Ajouter un secrtaire</h2>
          <input
            type="text"
            name="nom"
            placeholder="Nom"
            value={formData.nom}
            onChange={handleChange}
          />
          <input
            type="text"
            name="prenom"
            placeholder="Prnom"
            value={formData.prenom}
            onChange={handleChange}
          />
          <input
            type="email"
            name="email"
            placeholder="Email"
            value={formData.email}
            onChange={handleChange}
          />
          <input
            type="password"
            name="motDePasse"
            placeholder="Mot de passe"
            value={formData.motDePasse}
            onChange={handleChange}
          />
          <button onClick={handleAddSecretaire}>Ajouter</button>
        </div>
      )}

  

      {action === "delete" && (
        <div>
          <h2>Supprimer un secrtaire</h2>
          <select
            name="id"
            value={formData.id}
            onChange={handleChange}
          >
            <option value="">Slectionnez un secrtaire</option>
            {secretaires.map(secretaire => (
              <option key={secretaire._id} value={secretaire._id}>
                {secretaire.nom} {secretaire.prenom} - {secretaire.email}
              </option>
            ))}
          </select>
          <button
            onClick={() => {
              if (formData.id) {
                handleDeleteSecretaire(formData.id);
              } else {
                alert("Veuillez slectionner un secrtaire  supprimer");
              }
            }}
          >
            Supprimer
          </button>
        </div>
      )}

      {/* Liste des secrtaires */}
      <div>
        <h2>Liste des secrtaires</h2>
        <ul>
          {secretaires.map((secretaire) => (
            <li key={secretaire._id}>
              {secretaire.nom} {secretaire.prenom} - {secretaire.email}
            </li>
          ))}
        </ul>
      </div>

      {/* Choix de l'action */}
      <div>
        <button onClick={() => { setAction("add"); setFormData({ nom: "", prenom: "", email: "", motDePasse: "", id: "" }); }}>
          Ajouter un secrtaire
        </button>
        <button onClick={() => { setAction("delete"); setFormData({ nom: "", prenom: "", email: "", motDePasse: "", id: "" }); }}>
          Supprimer un secrtaire
        </button>
      </div>
    </div>
  );
}

export default Admin;
